import Vue from 'vue'
import App from './App.vue'
import VueResource from 'vue-resource'
import VueRouter from 'vue-router'
import {routes} from './routes'

Vue.use(VueResource)
Vue.use(VueRouter)

const router = new VueRouter({
  routes
  //, mode: 'history' //toda url digitada deve retornar a index pelo backend do servidor para essa linha funcionar
})

new Vue({
  el: '#app',
  router,
  render: h => h(App)
})



